<template>
  <div id="app" class="container">
    <router-view></router-view>
  </div>
</template>

<script>
import ListRow from '@/components/Row.vue';
import http from '@/util/http-common';

export default {
  name: 'App',
  components: {
    ListRow,
  },
  data: function() {
    return {
      items: [],
    };
  },
  created() {
    http.get('/api/qna')
      .then(({ data }) => {
        this.items = data;
      })
      .catch(() => { alert('에러가 발생했습니다.'); });
    this.$router.push('/api/qna/list');
  },
  methods: {
    movePage() {
      this.$router.push('/api/qna/create');
    },
  },
};
</script>

